/*****************************
******NAVIGATION/MENU.JS******
******************************/

.menu-header{
  font-size: 14px;
  font-family: ProximaNova-Semibold;
}

.navbar {
  background-color: #fff;
  height: 53px;
}

.nav-tabs .nav-link{
  border: 0px solid transparent;
  border-top-left-radius: .25rem;
  border-top-right-radius: .25rem;
}
.nav-link:hover {
  text-decoration: underline;
}

.nav-item {
  font-size: 12px;
  font-family: ProximaNova-Semibold;
}

.nav-item-main {
  font-size: 16px;
  font-family: ProximaNova-Semibold;
}

/*****************************
********** FA ICONS **********
******************************/

.fa-heart {
  color: #cc0000;
  font-size: 18px;
}

.far {
  color: #000;
  font-size: 18px;
}

/* Top right text */
.top-right {
  position: absolute;
  top: 5px;
  right: 6px;
}

.fa-edit {
  font-size: 16px;
}

.fa-trash-alt {
  font-size: 16px;
}

/*****************************
****** cart +badges **********
******************************/

.cart-badge,
.cart-badge:hover {
    border: radius 0%;
    padding: 1px;
    font-size: 10px;
    font-style: normal;
    color: #fff;
    background: #c9dce1;
}

/*****************************
********** CARD **************
******************************/

.card{
  border: none;
  border-radius: 0;
  padding: 0;
}

.card-dashboard{
  /* border: 1px solid #000; */
  border-radius: 0;
  padding: 0;
  flex-direction: column;
  min-width: 0;
  word-wrap: break-word;
  background-color: #fff;
  background-clip: border-box;
  position: relative;
}

.card-header {
  padding: .75rem .75rem;
  margin-bottom: 0;
  background-color: #f7fafa;

  border-bottom: 1px #000!important;
}

.card-body{
  padding-right: 15px;
  padding-left: 15px;
  border: none;
  border-radius: 0;
  padding: 0;
}

p.lead {
  font-family: ProximaNova-Regular;
  font-size: 12px;
}

p.details-lead {
  font-family: ProximaNova-Regular;
  font-size: 16px;
}

.in-category {
  font-size: 12px;
}

/*****************************
*** PRODUCT DETAILS **********
******************************/

.name {
  background: #c9dce1;
  color: #000;
  font-weight: bold;
}

.lead{
  font-family: 'ProximaNova-Regular', sans-serif !important;
  font-size: 14px;
}

.product-img {
  min-height: 100px;
  align-content: center;
}

.product-img-thumb {
  min-height: 50px;
}

.payment-module {
  display: flex;
  flex-direction: column;
  border: 1px solid #000;
  padding: 20px;
  width: 100%;
}

.details-img{
  vertical-align: left;
}

