/*****************************
**** SEARCHBAR ***************
******************************/

.input-file {
  background-color: #fff;
  color: #000;
  border-color: #000;
  font-size: 12px;
  letter-spacing: 1px;
}

.input-file:focus {
    background-color: #fff;
    border-color: #000;
    color: #000;
    font-size: 12px;
    box-shadow: none!important;
    outline: none!important;
}

.input,.input:active {
  border: 1px solid #000!important;
  box-shadow: none;
}

.input-group-md {
  width: 275px;
}

/*****************************
******** CHECKOUT ************
******************************/

.checkout-buttons-container {
  float: right;
  margin-top: 8px;
  /* border: 1px solid #000; */
  width: 100%;
}

.text-success {
  color: #0c5460;
  background-color: #fff;
  font-size: 14px;
  letter-spacing: 2px;
  width: 50%;
  padding: .75rem .75rem;
  border: 1px solid #0c5460!important;
  margin-bottom: 3vh;
  border-radius: 0;
  width: 100%!important;
}

.text-danger {
  color: #cc0000;
  border: 1px solid #cc0000!important;
}

.text-loading {
  color: #000;
  font-size: 16px;
  letter-spacing: 2px;
  font-family: ProximaNova-Semibold;
}

.alert {
  color: #cc0000;
  background-color: #fff;
  font-size: 14px;
  letter-spacing: 2px;
  width: 50%;
  padding: .75rem .75rem;
  border: 1px solid #cc0000!important;
  margin-bottom: 3vh;
  border-radius: 0;
  width: 100%!important;
}

.btn-group.show, .dropdown-menu.show {
  background-color: #4b4b4b;
}

.btn-group.show .dropdown-menu.show .dropdown-item {
  color: #e1e1e1;
}

.btn-group.show .dropdown-menu.show .dropdown-divider {
  border-top: 1px solid rgba(50, 50, 50, 0.9);
}

.btn-group.show .dropdown-menu.show .dropdown-item:hover,
.btn-group.show .dropdown-menu.show .dropdown-item:focus {
  background-color: #000000;
  color: #ffffff;
}

.input-group, .input-group-text, .input-group-prepend, .form-control {
  font-family: ProximaNova-Semibold;
  font-size: 16px;
  letter-spacing: 2px;
}

.form-group {
  font-family: ProximaNova-Regular;
  font-size: 12px;
}

.form-control:focus {
  /* border: none; */
  box-shadow: none;
  border: 1px solid #000!important;
  -webkit-appearance: none;
  transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
  transition: none!important;
}

.form-control {
  -webkit-appearance: none;
  border-radius: 0;
}

.input-group:focus {
  border: none;
  box-shadow: none;
}

.form-check-label {
  font-family: ProximaNova-Regular;
  font-size: 12px;
}

.form-control:disabled, .form-control[readonly] {
  background-color: #f7fafa!important;
  opacity: 1;
}

/*ADMIN USER DASHBOARD LINKS*/
.list-group-item {
  position: relative;
  display: block;
  /* padding: .25rem .5rem; */
  margin-bottom: -1px;
  background-color: #fff;
  border: 0!important;
}

/*****************************
******** TABLE ***************
******************************/

.table-wrapper {
  width: 700px;
  margin: 30px auto;
  /* background: #fff; */
  padding: 20px;	
  /* box-shadow: 0 1px 1px rgba(0,0,0,.05); */
}
.table-title {
  padding-bottom: 10px;
  margin: 0 0 10px;
  border-bottom: 0;
}
.table-title h2 {
  margin: 6px 0 0;
  font-size: 22px;
}
.table-title .add-new {
  float: right;
  height: 30px;
  font-weight: bold;
  font-size: 12px;
  text-shadow: none;
  min-width: 100px;
  border-radius: 50px;
  line-height: 13px;
}
/* .table-title .add-new i {
  margin-right: 4px;
} */
table.table {
  table-layout: fixed;
}

table.table td a {
  cursor: pointer;
  display: inline-block;
  margin: 0 5px;
  min-width: 24px;
}

table.table td i {
  font-size: 19px;
} 
table.table td a.add i {
  font-size: 24px;
  margin-right: -1px;
  position: relative;
  top: 3px;
}     

table.table .form-control.error {
  border-color: #cc0000;
}
table.table td .add {
  display: none;
}

.table-bordered td {
  border: 1px solid #fff !important;
}

.btn,
.nav :hover {
    border-radius: 0px;
}

/*****************************
**** SPLASH/CAROUSEL *********
******************************/

.splash {
  margin: auto;
  align-content: stretch;
  flex-direction: column;
}
.carousel-container {
  margin-top: 4vh;
}

.homepage-container {
  margin-top: 6vh;
  flex: block;
  align-items: flex-start;
  justify-content: center;
  width: 100%;
  /* padding-right: 15px;
  padding-left: 15px; */
  margin-right: auto;
  margin-left: auto;
}

.container {
  margin-top: 10vh;
}

.container-create {
  margin-top: 10vh;
}

.container-search {
  width: 100%;
}

.container-search-result {
  display: flex;
  background: #fff;
  margin-left: 9px;
  overflow-x: hidden;
  height: 500px;
  width: 400px;
  overflow-y: scroll !important;
}

.window {
  justify-content: space-between;
  
}

.related {
  margin-top: 5vh;
}
